"use client";

import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  Legend,
} from "recharts";

import { useState, useEffect, useMemo } from "react";
import { useSearchParams } from "next/navigation";
import { supabase } from "../../../lib/supabaseClient";
import { useCashflowData } from "../dashboard/components/useCashflowData";
import { categorizeExpense } from "../dashboard/components/expensePieChart";

/* =========================
   HELPERS
========================= */
function normalizeCategory(cat = "") {
  const t = cat.toLowerCase();

  if (t.includes("personal")) return "Personal Services";
  if (t.includes("mooe")) return "MOOE";
  if (t.includes("20")) return "20% BDF";
  if (t.includes("bdf")) return "20% BDF";
  if (t.includes("gadr") || t.includes("gad")) return "15% GAD";
  if (t.includes("bdr")) return "5% BDRRMF";
  if (t.includes("sk")) return "10% SK Fund";
  if (t.includes("senior")) return "1% Senior & PWD";

  return null; // â— VERY IMPORTANT
}


export default function ExpensesPage() {
  /* =========================
     YEAR
  ========================= */
  const searchParams = useSearchParams();
  const START_YEAR = 2025;
  const currentYear = new Date().getFullYear();
  const yearFromUrl = Number(searchParams.get("year"));

  const selectedYear =
    yearFromUrl && yearFromUrl >= START_YEAR
      ? yearFromUrl
      : currentYear;

  /* =========================
     TRANSACTIONS
  ========================= */
  const { transactions: fetchedTransactions } =
    useCashflowData(selectedYear);

  const [transactions, setTransactions] = useState([]);

  useEffect(() => {
    setTransactions(fetchedTransactions || []);
  }, [fetchedTransactions]);

  /* =========================
     ALLOCATION TOTALS
  ========================= */
  const [allocations, setAllocations] = useState([]);

  useEffect(() => {
    async function fetchAllocations() {
      const { data, error } = await supabase
        .from("budget_uploads")
        .select("id, category, amount, description")
        .eq("year", selectedYear);

      if (error) {
        console.error("Allocation fetch error:", error);
        return;
      }

      const totals = (data || []).filter(
        (row) =>
          row.description?.trim().toLowerCase() === "total"
      );

      setAllocations(
        totals.map((row) => ({
          id: row.id,
          category: normalizeCategory(row.category),
          allocated: Number(row.amount),
        }))
      );
    }

    fetchAllocations();
  }, [selectedYear]);
  

  /* =========================
     BREAKDOWN STATE
  ========================= */
  const [expandedCategory, setExpandedCategory] = useState(null);
  const [subcategories, setSubcategories] = useState({});

  async function fetchSubcategories(category) {
    const { data, error } = await supabase
      .from("budget_uploads")
      .select("id, description, amount")
      .eq("year", selectedYear)
      .eq("category", category)
      .neq("description", "Total");

    if (error) {
      console.error("Subcategory fetch error:", error);
      return;
    }

    setSubcategories((prev) => ({
      ...prev,
      [category]: data || [],
    }));
  }

  function toggleCategory(category) {
    setExpandedCategory((prev) => {
      const next = prev === category ? null : category;

      if (next && !subcategories[category]) {
        fetchSubcategories(category);
      }

      return next;
    });
  }

  /* =========================
     CHART DATA (SOURCE = ALLOCATIONS)
  ========================= */
  const chartData = useMemo(() => {
  const map = {};

  // 1ï¸âƒ£ Allocations = source of truth
  allocations.forEach((a) => {
    map[a.category] = {
      category: a.category,
      allocated: a.allocated,
      spent: 0,
    };
  });

  // 2ï¸âƒ£ Apply expenses using SAME vocabulary
  transactions.forEach((t) => {
    if (!t.withdrawal || t.withdrawal <= 0) return;

    const raw = categorizeExpense(t.description || "");
    const category = normalizeCategory(raw);

    if (!category) return;
    if (!map[category]) return;

    map[category].spent += Number(t.withdrawal);
  });

  return Object.values(map).sort(
    (a, b) => b.allocated - a.allocated
  );
}, [allocations, transactions]);

  /* =========================
     UI
  ========================= */
  return (
    <div className="space-y-6">
      {/* HEADER */}
      <div className="rounded-xl bg-emerald-700 px-6 py-4 text-white shadow">
        <h1 className="text-xl font-semibold">
          ðŸ’¸ Expenses vs Budget Allocation ({selectedYear})
        </h1>
        <p className="text-xs opacity-90 mt-0.5">
          Official budget allocation with detailed breakdown
        </p>
      </div>

      {/* BUDGET CARDS */}
      <div className="bg-white p-5 rounded-xl border shadow-sm">
        <h3 className="font-semibold text-base mb-4">
          ðŸ“Š Budget Allocation
        </h3>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {allocations.map((a) => {
            const isOpen = expandedCategory === a.category;

            return (
              <div
                key={a.id}
                className={`rounded-lg border transition
                  ${isOpen ? "border-emerald-400" : "border-gray-200"}
                `}
              >
                <div className="px-4 py-3 flex justify-between">
                  <div>
                    <p className="text-[18px] uppercase">
  {a.category || "Uncategorized"}
</p>

                    <p className="text-xl font-semibold text-emerald-600">
                      â‚±{a.allocated.toLocaleString()}
                    </p>
                  </div>

                  <button
                    className="text-[11px] text-emerald-600"
                    onClick={() => toggleCategory(a.category)}
                  >
                    {isOpen ? "Hide" : "Details"}
                  </button>
                </div>
               

                {isOpen && (
                  <div className="border-t px-4 py-2 bg-gray-50 text-xs space-y-1 max-h-56 overflow-y-auto">
                    {subcategories[a.category]?.length ? (
                      subcategories[a.category].map((s) => (
                        <div
                          key={s.id}
                          className="flex justify-between"
                        >
                          <span className="truncate max-w-[70%]">
                            {s.description}
                          </span>
                          <span className="font-medium">
                            â‚±{Number(s.amount).toLocaleString()}
                          </span>
                        </div>
                      ))
                    ) : (
                      <p className="italic text-gray-400">
                        No breakdown available
                      </p>
                    )}
                  </div>
                )}
              </div>
            );
          })}
        </div>
      </div>

      {/* CHART */}
      <div className="bg-white p-6 rounded-xl border shadow-sm">
        <h3 className="font-semibold text-lg mb-4">
          ðŸ“ˆ Allocation vs Expenses
        </h3>

        <ResponsiveContainer width="100%" height={320}>
          <BarChart data={chartData}>
            <XAxis dataKey="category" />
            <YAxis />
            <Tooltip formatter={(v) => `â‚±${v.toLocaleString()}`} />
            <Legend />
            <Bar dataKey="allocated" fill="#10b981" />
            <Bar dataKey="spent" fill="#ef4444" />
          </BarChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}
